body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
  --square: 84px;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
  width: calc(var(--square)*3);
}

#status {
  margin-bottom: 10px;
  background: orange;
  transition: background 3s ease-in;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: var(--square);
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: var(--square);
  cursor: pointer;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
  width: calc(var(--square)*2);
}

.game-info li{
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
}

.control {
  background-color: white;
  border: 0.5px dashed;
  /* color: orange; */
  /* padding: 15px 32px; */
  text-align: center;
  text-decoration: none;
  display: inline-block;
  /* font-size: 16px; */
  cursor: pointer;
  padding: 5px;
  margin: 5px;
}

#root {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#head {
  margin-bottom: 2em;
  }
